import { StatusBar } from "expo-status-bar";
import { StyleSheet, View, ScrollView, Text } from "react-native";
import { StackScreenProps } from "@react-navigation/stack";
import { StackParams } from "../../../types/stack.params";
import { MaterialIcons as Icon } from "@expo/vector-icons";
import { SwipeButton } from "../../../components/buttons/swipe-button";
import { useContext, useState } from "react";
import { Dialog } from "../../../components/modals/dialog";
import { GetTokenJWT } from "../../../services/token-jwt.service";
import { ReleaseDiscount } from "../../../services/budget.service";
import { AuthContext } from "../../../contexts/auth.provider";
import { LogTypeEnum, UserLogs } from "../../../models/user.logs.model";
import axios from "axios";

interface Properties
  extends StackScreenProps<StackParams, "DiscountConfirmation"> {}

export default function DiscountConfirmation({
  navigation,
  route,
}: Properties) {
  const { _branch, _budget, _budgetObject, _discountValue } = route.params;
  const authContext = useContext(AuthContext);
  const defaultDialog = { title: "", content: "", visible: false };
  const [dialog, setDialog] = useState(defaultDialog);
  const data = authContext.user?.logs!;
  const date = new Date();
  const day = date.getDate();
  const month = date.getMonth() + 1;
  const year = date.getFullYear();

  const releaseDiscount = () => {
    GetTokenJWT(authContext.user?.uid!, authContext.urlBackend!)
      .then((authToken) => {
        ReleaseDiscount(
          _budget,
          _branch,
          _discountValue,
          authToken?.token!,
          authContext.urlBackend!
        )
          .then(() => {
            let i = data.length.toString();
            let newLog: UserLogs = {
              id: i,
              title: `Desconto no oramento ${_budget}`,
              date: `${day}/${month}/${year}`,
              description: `R$ ${_budgetObject.totalBruto - _discountValue}`,
              type: LogTypeEnum.DESCONTO_ORCAMENTO,
            };
            data.push(newLog);
            authContext.addLog(data);
            Alert("Sucesso", "Efetuado desconto para o oramento: " + _budget);
          })
          .catch((error) => {
            if (axios.isAxiosError(error)) {
              Alert("Erro", "Servidor indisponvel");
            } else {
              Alert("Erro", error.response.data.error);
            }
          });
      })
      .catch((error) => {
        if (axios.isAxiosError(error)) {
          Alert("Erro", "Servidor indisponvel");
        } else {
          Alert("Erro", error.response.data.error);
        }
      });
  };

  const Alert = (title: string, content: string) => {
    setDialog({ title: title, content: content, visible: true });
  };

  if (dialog.title != "Erro") {
    return (
      <ScrollView
        contentContainerStyle={styles.container}
        scrollEnabled={false}
      >
        <View style={styles.topField}>
          <View style={styles.header}>
            <Icon
              testID="icon"
              name="keyboard-arrow-left"
              size={48}
              color="white"
              onPress={() => {
                navigation && navigation.navigate("Home");
              }}
            />
            <Text style={styles.headerText}>Desconto</Text>
          </View>
          <Text style={styles.client}>{_budgetObject.nomeCliente}</Text>
          <Text style={styles.budget}>Oramento #{_budget}</Text>
        </View>
        <View style={styles.bottomField}>
          <View>
            <Text style={styles.result}>{_budgetObject.tipoOrcamento}</Text>
            <Text style={styles.description}>Tipo do oramento</Text>
          </View>
          <View>
            <Text style={styles.result}>{_budgetObject.nomeVendedor}</Text>
            <Text style={styles.description}>Vendedor</Text>
          </View>
          <View>
            <Text style={styles.result}>
              Total R${" "}
              {(_budgetObject.totalBruto - _discountValue)
                .toFixed(2)
                .replace(".", ",")}
            </Text>
            <Text style={styles.description}>
              Pagamento
              <Text style={{ fontWeight: "bold" }}>
                {" "}
                em {_budgetObject.pagamentos[0].forma}
              </Text>
            </Text>
          </View>
          <SwipeButton
            onComplete={releaseDiscount}
            title="EFETUAR DESCONTO"
            underlayTitle="LIBERAR"
          />
        </View>
        <Dialog
          visible={dialog.visible}
          title={dialog.title}
          content={dialog.content}
          dismiss={() => {
            navigation.navigate("Discount", { _branch, _budget });
            setDialog(defaultDialog);
          }}
        />
        <StatusBar
          style="light"
          translucent={false}
          backgroundColor="#212A4D"
        />
      </ScrollView>
    );
  } else {
    return (
      <View style={styles.container}>
        <Dialog
          visible={dialog.visible}
          title={dialog.title}
          content={dialog.content}
          dismiss={() => {
            navigation.navigate("Home");
            setDialog(defaultDialog);
          }}
        />
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    flexDirection: "column",
    backgroundColor: "#F0F2F7",
  },
  header: {
    flexDirection: "row",
    alignItems: "center",
  },
  headerText: {
    fontSize: 16,
    color: "white",
  },
  topField: {
    flex: 2,
    backgroundColor: "#1F2D5A",
  },
  client: {
    fontSize: 28,
    fontWeight: "bold",
    color: "white",
    paddingHorizontal: "5%",
    paddingTop: 52,
  },
  budget: {
    fontSize: 16,
    fontWeight: "bold",
    color: "white",
    paddingHorizontal: "5%",
    paddingTop: 64,
  },
  bottomField: {
    flex: 4,
    flexDirection: "column",
    marginHorizontal: "5%",
    justifyContent: "space-around",
  },
  result: {
    fontSize: 28,
    fontWeight: "bold",
    color: "#454545",
  },
  description: {
    fontSize: 16,
    color: "#454545",
  },
});
